*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: "Roboto", Arial, sans-serif;
  -webkit-font-smoothing: antialiased; }

body {
  background-color: white; }

* > ul {
  list-style-type: none; }

* > a {
  text-decoration: none; }

img {
  cursor: pointer; }

button:active {
  outline: none; }

button:focus {
  outline: none; }

.container {
  max-width: 660px;
  margin: 0 auto; }

