      text-transform: uppercase;
      color: #80808c; }
    .footer .footer__follow .footer__follow-sun .footer_svg {
      margin: 0 1.5rem;
      width: 12%;
      height: 12%; }
      @media screen and (min-width: 500px) {
        .footer .footer__follow .footer__follow-sun .footer_svg {
          width: 9%;
          height: 9%; } }
      @media screen and (min-width: 676px) {
        .footer .footer__follow .footer__follow-sun .footer_svg {
          margin-top: 2rem; } }
  .footer .footer__nav .footer__nav-services {
